<h1 id="exam-2021-08-22" tabindex="-1"><a class="header-anchor" href="#exam-2021-08-22" aria-hidden="true">#</a> Exam 2021-08-22</h1>
<p>Here you find sample answers and marking guidelines to the questions on the exam 2021-08-22.</p>
<div class="custom-container danger"><p class="custom-container-title">Point reduction for improper usage of upper-/lowercase characters</p>
<p>As explained in the exam instructions, answers not properly using upper-/lowercase characters will receive point reduction. This time it has only been set to <code>0.1</code> points for each answer that do not properly use upper-/lowercase characters, such as answers written with only lowercase characters. Next time this will likely increase.</p>
