    x<span class="token operator">:</span> request<span class="token punctuation">.</span>query<span class="token punctuation">.</span>x<span class="token punctuation">,</span>
    y<span class="token operator">:</span> request<span class="token punctuation">.</span>query<span class="token punctuation">.</span>y<span class="token punctuation">,</span>
