<template v-slot:question>
<p>Carl is assigned the task to design a REST API. To delete movies in a specific genre a specific year, he decides clients should send an HTTP <code>DELETE</code> request to <code>/movies</code>, add the header <code>Content-Type: application/json</code> and in the body of the request add <code>{&quot;year&quot;: 2000, &quot;genre&quot;: &quot;drama&quot;}</code> (change <code>2000</code> and <code>&quot;drama&quot;</code> to desired values).</p>
<p>Is this a good or bad REST API design? Justify your answer.</p>
<p><strong>Note</strong>: You don't need to worry about authorization in this question.</p>
</template>
</ExamQuestion>
