</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><p>Create the function <code>getMovieByIdWithProducerAndCategory()</code>, that fetches a movie with a specific id, and then fetches the producer and the category the movie belongs too, so it can be used like this:</p>
