<h2 id="question-19-1p" tabindex="-1"><a class="header-anchor" href="#question-19-1p" aria-hidden="true">#</a> Question 19 (1p)</h2>
<ExamQuestion>
<template v-slot:question>
<p>The JavaScript function <code>setTimeout()</code> can be used to schedule a call to a function in the future. Example:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token function">setTimeout</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token comment">// The code here in the function runs 1000 ms later.</span>
<span class="token punctuation">}</span><span class="token punctuation">,</span> <span class="token number">1000</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><p>The function <code>getInfo()</code> can be used to fetch some info from a server. Sample usage:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token function">getInfo</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">info</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token comment">// This code runs when we have got the info.</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
